import maskLayer from "../../components/common/mask-layer.vue"
export default {
	props:{
		isRefresh:{
			type:Boolean,
			default:false
		}
	},
	data() {
		return {
			showTip:false,
			showMask:false

		}
	},
	watch:{
		isRefresh(newVal){
			if(newVal){
				this.showMask = true
				this.showTip = true 
			}
		}
	},
	methods: {
		
		getuserinfo(e){
			let {userInfo} = e.detail;
			let playInfo = [] ;
			userInfo.like = []
			wx.cloud.callFunction({ // 调用云函数读取用户信息
			  // 要调用的云函数名称
			  name: 'login',
			  success: res => { 
				const {openId} = res.result.event.userInfo
				this.$api.reqUser(openId).then(response=>{
					response = response[response.length-1]
					console.log(response);
					if(!response){
						userInfo = Object.assign(userInfo,res.result.event.userInfo)
					}else{
						userInfo  = response.userInfo
						playInfo = response.playInfo
						console.log(playInfo);
						if(response._id){
							uni.setStorage({
								key: '_id',
								data: response._id,
								success: (result)=> {
									console.log(result);	
								}
							})
						}
						
					}
					getApp().globalData.userInfo  = userInfo
					getApp().globalData.playInfo = playInfo
				}).catch(error=>{//利用assign 将两个对象合并起来
					console.log(error);
				})
				
				uni.setStorage({
				    key: 'user',
					data:{
						 userInfo ,
						 playInfo
					},
				    success: function (res) {		
				      // console.log(res);
				    }
				});
			  },
			  fail: err => {
				uni.showModal({
					title:'获取用户信息失败'
				})
			  }
			})	
			this.showTip = false
			this.showMask = !this.showMask
		},
	},
	onReady() {
		let userInfo = getApp().globalData.userInfo
		if(!userInfo ) {
			this.showTip = true
			this.showMask = true	
		}	
	},
	components:{
		maskLayer,
	}
}
